import React from "react";
import { SignedIn, SignedOut, UserButton, SignInButton, SignUpButton } from "@clerk/nextjs";
import Link from "next/link";
import Image from "next/image";
import { Button } from "./ui/button";
import {
  PenBox,
  LayoutDashboard,
  FileText,
  GraduationCap,
  ChevronDown,
  StarsIcon,
} from "lucide-react";

const Header = () => {
  return (
    <header>
      <nav>
        <Link href={"/"}>
        <Image src={'/logo.png'}
        alt="logo"
        width={200}
        height={60}
        className="h-12 py-1 w-auto object-contain"/>

          
          </Link>
          <dir>
            <SignedIn>
              <Link>
              <Button>
                <LayoutDashboard/>
                </Button>
                </Link>
            </SignedIn>
          </dir>
      </nav>


      <SignedOut>
        <SignInButton />
       
      </SignedOut>

      <SignedIn>
        <UserButton />
      </SignedIn>
    </header>
  );
};

export default Header;
